import React from 'react'
import Hammer from 'hammerjs'
import { TweenLite } from 'gsap'
import classNames from 'classnames'
import '../App.css'

class DropdownMenu extends React.Component {
  componentDidMount() {
    this.hammer = Hammer(this.refs.dropdownMenu)
    this.hammer.get('swipe').set({ direction: Hammer.DIRECTION_VERTICAL })
    this.hammer.on('swipeup', (e) => this.handleSwipeUp(e))
  }

  handleSwipeUp(e) {
    TweenLite.to(this.refs.dropdownMenu, .1, {y: 0})
    this.props.setAppState({menuIsHidden: true})
  }

  render() {
    const { sortBy, filters, menuIsHidden, setSortBy, toggleFilter, setAppState } = this.props

    return (
      <div ref="dropdownMenu" className={classNames({
        'Menu-dropdown': true,
        'hidden': menuIsHidden
      })}>
        <div className="Submenu-sort">
          <div className="Submenu-title">SORT BY</div>
          <div className="Submenu-contents">
            <div
              className={classNames({
                'Submenu-item': true,
                'active': sortBy === 'title'
              })}
              onClick={() => setSortBy('title')}
            >Title</div>
            <div
              className={classNames({
                'Submenu-item': true,
                'active': sortBy === 'composer'
              })}
              onClick={() => setSortBy('composer')}
            >Composer</div>
            <div
              className={classNames({
                'Submenu-item': true,
                'active': sortBy === 'year'
              })}
              onClick={() => setSortBy('year')}
            >Year</div>
          </div>
        </div>
        <div className="Submenu-filter">
          <div className="Submenu-title">FILTERS</div>
          <div className="Submenu-contents">
            <div
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('jazz-standards') > -1
              })}
              onClick={() => toggleFilter('jazz-standards')}
            >Jazz Standards</div>
            <div
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('american-songbook') > -1
              })}
              onClick={() => toggleFilter('american-songbook')}>American Songbook</div>
            <div
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('blues') > -1
              })}
              onClick={() => toggleFilter('blues')}>Blues</div>
            <div
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('rhythm') > -1
              })}
              onClick={() => toggleFilter('rhythm')}>Rhythm changes</div>
            <div
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('latin') > -1
              })}
              onClick={() => toggleFilter('latin')}>Latin</div>
            <div
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('waltz') > -1
              })}
              onClick={() => toggleFilter('waltz')}>Waltzes</div>
            <div
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('holiday') > -1
              })}
              onClick={() => toggleFilter('holiday')}>Holiday tunes</div>
            <div
              className={classNames({
                'Submenu-item': true,
                'active': filters === ['jazz-standards', 'american-songbook', 'blues', 'rhythm', 'latin', 'waltz', 'holiday']
              })}
              onClick={() => setAppState({filters: ['jazz-standards', 'american-songbook', 'blues', 'rhythm', 'latin', 'waltz', 'holiday']})}>All</div>
          </div>
        </div>
      </div>
    )
  }
}

export default DropdownMenu


